body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes  slideUpFromBottom {
  0% {
    -webkit-transform: translateY(25%);
            transform: translateY(25%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes  slideUpFromBottom {
  0% {
    -webkit-transform: translateY(25%);
            transform: translateY(25%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

body {
  background: -webkit-linear-gradient(92deg, #3A2067 0%, rgba(154,155,157,0.87) 53%, #0B1983 100%, #0B1882 100%);
  background: linear-gradient(-2deg, #3A2067 0%, rgba(154,155,157,0.87) 53%, #0B1983 100%, #0B1882 100%);
  color: white;
  margin: 0 auto;
  font-family: 'Futura', sans-serif;
}

.header ~ * {
  -webkit-animation: 1s ease-out 0s 1 slideUpFromBottom;
          animation: 1s ease-out 0s 1 slideUpFromBottom;
}

.anchor {
  display: block;
  position: relative;
  top: -100px;
  visibility: hidden;
}
#contact {
  padding: 20px 0 0 0;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 750px) {
  #contact {
    margin-left: 0;
    margin-right: 0;
  }
}

#contact a {
  color: white;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

#contact a:hover {
  background: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

#contact p {
  padding: 20px 10%;
}

#contact hr {
  width: 20%;
  height: 5px;
  background: white;
  border: 0;
  margin: 0 auto;
}
.footer-wrapper {
  text-align: center;
  padding-top: 20px;
}

.footer-wrapper p {
  margin: 0;
  padding: 0 0 50px 0;
}
.header {
  background: black;
  color: white;
  height: 70px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding-left: 10%;
  z-index: 1000;
}

@media screen and (max-width: 750px) {
  .header {
      padding-left: 0;
      text-align: center;
  }
}

.menu {
  list-style-type: none;
  margin-left: 55%;
  margin-top: -55px;
}

.menu li {
  float: left;
}

.menu li a {
  padding: 0 20px;
  color: white;
  text-decoration: none;
  border-left: 1px solid white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.menu li a:hover { 
  background: white;
  color: black;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}


@media screen and (max-width: 750px) { 
  .menu { 
      display: table;
      margin: 0 auto;
  }

  .menu li { 
      background: black;
  }
}
.intro-wrapper { 
  margin-top: 70px;
  padding: 20px 10% 60px 10%;
}

.intro {
  display: flex;
  flex-flow: wrap;
}
.intro-text-container {
  flex-basis: 33%;
}

@media screen and (max-width: 750px) {
  .intro-text-container {
    flex-basis: 100%;
    order: 1;
  }
}

.intro-text-container a {
  color: white;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.intro-text-container a:hover {
  background: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.reference-container {
  flex-direction: column;
  flex-basis: 30%;
  display: flex;
  flex-wrap: wrap;
  margin-left: 10px;
  padding: 60px 5px 20px 5px;
}

@media screen and (max-width: 900px) {
  .reference-container {
    flex-basis: 100%;
    margin: 10px auto 0 auto;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 5px 5px 20px 5px;
  }
}

@media screen and (max-width: 750px) {
  .reference-container {
    order: 3;
  }
}

.reference-style {
  margin-top: 10px;
  height: 30px;
  width: auto;
}

.link-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.link-container a {
  flex-basis: 10%;
}

.link-text {
  margin-left: 10px;
  color: white;
  font-size: 16px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  -webkit-text-decoration: underline white;
          text-decoration: underline white;
}

.link-text:hover {
  background: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.headshot-container {
  flex-basis: 30%;
  padding: 10px 20px 20px 0;
}

@media screen and (max-width: 750px) {
  .headshot-container {
    flex-basis: 70%;
    margin: 10px auto 0 auto;
    order: 2;
  }
}

.headshot-container .headshot {
  width: 100%;
  height: auto;
  border-radius: 3px;
  box-shadow: 5px 5px 5px #181818;
}
.featured-projects-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 2em;
}

.projects-and-work-container {
  padding: 20px 0 80px 0;
}

.project-list-container {
  display: flex;
  flex-wrap: wrap;
}

.secondary-projects-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 50px auto 0 auto;
  width: 80%;
  justify-content: space-evenly;
}

@media screen and (max-width: 750px) {
  .secondary-projects-container {
    width: 90%;
  }
}
.card {
  --width: 14em;
  width: var(--width);
  height: calc(var(--width) * 3 / 2);
  background: white;
  border-radius: 10px;
  font-family: 'Futura', sans-serif;
  color: black;
  padding: 1.5em 1em;
  position: relative;
  box-shadow: 10px 10px 15px 5px black;
  margin-top: auto;
  margin-bottom: auto;
}

@media screen and (max-width: 620px) {
  .card {
    margin-bottom: 2em;
  }
}

.card h1 {
  text-decoration: underline;
  font-weight: normal;
  margin-bottom: 0;
  margin-top: 0;
  position: relative;
  z-index: 1;
}

.card p {
  margin-top: 0;
  margin-right: 20px;
  position: relative;
  z-index: 1;
}

.card .tech-stack {
  position: absolute;
  bottom: 1em;
}

.card .bulb-container .github-link,
.card .bulb-container .project-link{
  position: relative;
  -webkit-transition: 3s;
  transition: 3s;
}

.card .bulb-container .github-link:hover,
.card .bulb-container .project-link:hover {
  top: 0.25em;
  -webkit-transition: 3s;
  transition: 3s;
}

@media screen and (max-width: 650px) {
  .card {
    margin-left: auto;
    margin-right: auto;
  }
}
.bulb-logo {
  width: 1.5em;
  height: 1.5em;
  margin-left: 0.5em;
  position: absolute;
  bottom: 1em;
}

.bulb {
  width: 3.5em;
  height: auto;
  margin-right: 1em;
}

.bulb-container {
  position: absolute;
  bottom: -1.80em;
}

.bulb-container .dummy {
  visibility: hidden;
}
.antrepreneur-card-art .color {
  position: absolute;
  top: 1em;
  right: 1em;
  z-index: 0;
  width: 10em;
  height: auto;
}
.latte-log-card-art .color {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
}

.latte-log-card-art .larger {
  width: 12em;
  height: auto;
  border-top-right-radius: 10px;
}

.latte-log-card-art .smaller{
  width: 10em;
  height: auto;
  right: 2em;
  top: 4em;
}

.new-project-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 75%;
  margin: 0 auto 2em auto;
  justify-content: flex-start;
}

@media screen and (max-width: 1150px) {
  .new-project-container {
    justify-content: space-evenly;
  }
}

.desktop {
  --width: 45vw;
  width: var(--width);
  height: calc(var(--width) * 9 / 16);
  border-radius: 10px;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 2em;
  box-shadow: 10px 10px 15px 5px black;
}

@media screen and (max-width: 1600px) {
  .desktop {
    --width: 50vw;
  }
}

@media screen and (max-width: 1150px) {
  .desktop {
    display: none;
  }
}

.mobile {
  --width: 12vw;
  width: var(--width);
  height: calc(var(--width) * 1334 / 750);
  margin-top: auto;
  margin-bottom: auto;
  border-radius: 10px;
  box-shadow: 10px 10px 15px 5px black;
}

@media screen and (max-width: 1600px) {
  .mobile {
    display: none;
  }
}

@media screen and (max-width: 1150px) {
  .mobile {
    display: block;
    --width: 13em;
  }
}
.seal-spotter-card-art .color {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
}

.seal-spotter-card-art .larger {
  width: 12em;
  height: auto;
  border-top-right-radius: 10px;
}

.seal-spotter-card-art .smaller{
  width: 10em;
  height: auto;
  right: 2em;
  top: 4em;
}

.video-game-card-art .color {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  width: 10em;
  height: auto;
}

.work a {
  padding: 0 20px;
  color: white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.work a:hover { 
  text-decoration: underline;
  color: black;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  margin-bottom: 0;
}

.work a h2 {
  margin-bottom: 0;
}

.work a span:hover {
  background:white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  color: black;
}

.work .description {
  margin-top: 0;
}

.work-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
